import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ReactCodeInput from 'react-verification-code-input';
import { withRouter } from 'react-router';
import $ from 'jquery';

import Main from './main.component';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import { Button } from '@material-ui/core';
import mail from '../../images/login/icon.svg'

// language for test
import text from '../../languages/english/translate.json';


class PinCode extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            codeFor: ''
        }
    }
    componentDidMount() {
        console.log(this.props);
        if (this.props.location?.state) {
            let data = this.props.location.state;
            let codeFor = "";
            console.log(data)
            if (data.codeFor === 'passwordresets') {
                codeFor = 'reset';
                this.setState({ ...this.state, email: data.email, codeFor: codeFor })
            }
            else if (data.state?.codeFor === 'Register') {
                codeFor = 'registration';
                this.setState({
                    ...this.state, email: data.email, codeFor: codeFor,
                    codeForServer: data.state.codeFor, firstName: data.state.firstName, lastName: data.state.lastName,
                    password: data.state.password, dateOfBirth: data.state.dateOfBirth, gendar: data.state.gendar, userType: data.state.userType
                })
            }

        }
    }
    render() {
        let data = text.pinCode;
        const registerWithEmail = (code) => {
            // register as student
            let userType = '';
            let to = '';
            if (this.state.userType === 'Student') {
                userType = 'studentsInput';
                to = '/home'
            }
            // regiter as an instructor
            else {
                userType = 'subInstructorsInput';
                to = '/basicInformation'
            }
            $.ajax({
                url: 'https://quranacademy.black-squares.com/graphql',
                method: 'post',
                data: {
                    query: `
                        mutation($${userType} : ${userType}  $email : Email!
                            $pin_code :String!){
                            globalRegister(data: {
                                ${userType} : $${userType} ,
                                verifyPinCodeInput : {
                                email : $email
                                pin_code : $pin_code
                                }
                            }){
                                
                                BearerTokenResponse{
                                access_token
                                refresh_token
                                expires_in
                                }
                                SubInstructor{
                                    id
                                    email
                                    first_name
                                    last_name
                                    gender
                                    date_of_birth
                                }
                                Status{
                                code
                                message
                                check
                                }
                            }
                            }
                            `,
                    variables: {
                        [userType]:
                        {
                            "first_name": this.state.firstName,
                            "last_name": this.state.lastName,
                            "password": this.state.password,
                            "date_of_birth": this.state.dateOfBirth
                        },
                        "pin_code": code,
                        "email": this.state.email
                    }
                },
                dataType: 'json',
                success: res => {
                    console.log(res)
                    if (res.data?.globalRegister?.Status.code === 200) {
                        localStorage.setItem('jwt', JSON.stringify(res.data.globalRegister.BearerTokenResponse));
                        this.props.history.push({
                            pathname: to,
                            state : res.data?.globalRegister
                        });
                    }
                },
                cache: err => console.log(err)
            });
        }
        const handleVerify = () => {

            let inputs = document.querySelectorAll('input');
            let code = "";
            for (var x = 0; x < inputs.length; x++) {
                code = code + inputs[x].value
            }
            if (code.length === 5) {
                console.log(code)
                console.log(this.state.email)
                $.ajax({
                    url: 'https://quranacademy.black-squares.com/graphql',
                    method: 'post',
                    data: {
                        query: `
                        mutation($email : Email! , $pin_code : String!){
                          globalverifyPinCode(data:{
                                email : $email
                            pin_code : $pin_code
                          }){
                            Status{
                              code ,
                              message
                            }
                          }
                        }`,
                        variables: {
                            "email": this.state.email,
                            "pin_code": code
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        if (res.data) {
                            console.log(res.data.globalverifyPinCode)
                            if (res.data.globalverifyPinCode.Status.code === 200) {
                                console.log(this.state)
                                if (this.state.codeFor === 'registration') {
                                    // register with email
                                    registerWithEmail(code);
                                } else {
                                    // go to reset password
                                    this.props.history.push({
                                        pathname: this.state.codeFor,
                                        state: {
                                            email: this.state.email,
                                            code: code
                                        }
                                    });
                                }
                            } else {
                                this.setState({ ...this.state, error: res.data.globalverifyPinCode.Status.message })
                            }
                        }
                    },
                    cache: err => console.log(err)
                });
            }
        }
        return (
            <Main
                content={
                    <Row className="alig-items-center justify-content-center mt-5 mb-3">
                        <Col md={4} className="bg-white  shadow text-center pt-5 pb-5 pr-4 pl-4">
                            <div className="mb-2">
                                <img src={mail} alt="mail" className="img-flid" />
                            </div>
                            <div>
                                <h4>{data.title}</h4>
                                <p>{data.description}
                                    {this.state.email && this.state.email}</p>
                            </div>
                            <div className="mb-4">
                                <ReactCodeInput
                                    fields={5}
                                    fieldHeight={45}
                                    fieldWidth={45}
                                    className="w-auto "
                                    onComplete={handleVerify}
                                />
                                <div className="text-danger text-center"><p className="mb-1 mt-1">{this.state.error}</p></div>
                            </div>
                            <div className="">
                                <Button
                                    fullWidth
                                    className="btn-green"
                                    onClick={handleVerify}>
                                    {data.verify}
                                </Button>
                            </div>
                            <div>
                                <div className="text-center mt-3">
                                    {this.state.codeFor}
                                    <Link className="text-green" to={{
                                        pathname: '/confirmation',
                                        state: this.state.codeForServer
                                    }}> {data.resend} </Link>
                                </div>
                            </div>
                        </Col>
                    </Row>
                } />
        )
    }
}

export default withRouter(PinCode)