import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import GoogleLogin from 'react-google-login';
import $ from 'jquery';

import Main from './main.component';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, Checkbox, FormControlLabel, OutlinedInput,
    Button, FormControl, InputLabel, InputAdornment, IconButton
} from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import shape from '../../images/login/OBJECTS.svg';

// language for test
import text from '../../languages/english/translate.json';


class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            rememberMe: true,
            errors: {
                email: '',
                password: '',
                form: ''
            }
        }
    }
    componentWillMount() {

    }
    render() {
        let data = text.login;
        const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);
        const validPassword = RegExp(/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/);

        const handleClickShowPassword = () => {
            this.setState({ ...this.state, showPassword: !this.state.showPassword });
        };
        const handleMouseDownPassword = event => {
            event.preventDefault();
        };
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleChange = props => event => {
            const value = event.target.value;

            if (props === 'rememberMe') {
                this.setState({ ...this.state, [props]: event.target.checked });
            } else {
                this.setState({ ...this.state, [props]: event.target.value });
            }

            let errors = this.state.errors;
            errors.form = ''
            errors.loginSucc = ''
            switch (props) {
                case 'email':
                    errors.email =
                        validEmailRegex.test(value)
                            ? ''
                            : text.validation.email;
                    break;
                case 'password':
                    errors.password =
                        validPassword.test(value)
                            ? ''
                            : text.validation.password;
                    break;
                default:
                    break;
            }
        }
        const handleLogin = (event) => {
            event.preventDefault();
            if (validateForm(this.state.errors)) {
                $.ajax({
                    url: 'https://quranacademy.black-squares.com/graphql',
                    method: 'post',
                    data: {
                        query: `
                        mutation($email : Email! $password :  Password!){
                                globalLogin(data:{
                                  email : $email
                                 password : $password
                                }){
                                  BearerTokenResponse{
                                    access_token
                                    refresh_token
                                  }
                                  Student{
                                    id
                                    email
                                    first_name
                                  }
                                  Instructor{
                                    id
                                    email
                                    first_name
                                  }
                                  SubInstructor{
                                    id
                                    email
                                  }
                                  Status{
                                    code
                                    message
                                  }
                                }
                              }
                            `,
                        variables: {
                            "email": this.state.email,
                            "password": this.state.password
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        if (res.data.globalLogin.Status.code === 200) {
                            // login successfully
                            let errors = this.state.errors;
                            errors.loginSucc = res.data.globalLogin.Status.message
                            this.setState({ ...this.state, errors: errors })
                            // if remember me checked
                            if (this.state.rememberMe) {
                                localStorage.setItem('jwt', JSON.stringify(res.data.globalLogin));
                                // home page with the data
                                this.props.history.push({
                                    pathname: '/home',
                                });
                            } else {
                                // home page 
                                this.props.history.push({
                                    pathname: '/home',
                                });
                            }
                        } else {
                            let errors = this.state.errors;
                            errors.form = res.data.globalLogin.Status.message
                            this.setState({ ...this.state, errors: errors })
                        }
                    },
                    cache: err => console.log(err)
                });
            }
            else {
                console.log("Form not valid!")
            }
        }
        const responseGoogle = (response) => {
            if (response.accessToken) {
                this.setState({ ...this.state, accessToken: response.accessToken });
                $.ajax({
                    url: 'https://quranacademy.black-squares.com/graphql',
                    method: 'post',
                    data: {
                        query: `mutation($token : String! $provider : String!){
                            globalLoginSocialite(data: {
                              token :$token ,
                              provider : $provider
                            }){
                              BearerTokenResponse{
                                access_token
                              }
                              Student{
                                email
                              }
                              Instructor{
                                email
                              }
                              SubInstructor{
                                email
                              }
                              Status{
                                code
                                message
                              }
                            }
                          }
                            `,
                        variables: {
                            "token": response.accessToken,
                            "provider": "google"
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        console.log(res)
                        // if not register  before 
                        if (res.data.globalLoginSocialite.BearerTokenResponse === null) {
                            this.props.history.push({
                                pathname: '/registration',
                                state: response
                            });
                        }
                        // if regitsered before
                        else {
                            localStorage.setItem('jwt', JSON.stringify(res.data.globalLoginSocialite));

                            this.props.history.push({
                                pathname: '/home'
                            });
                        }
                    },
                    cache: err => console.log(err)
                });
            }
        }
        console.log(this.state);
        return (
            <Main
                content={
                    <Row className="justify-content-center align-items-center">
                        <Col md={6}>
                            <div>
                                <img src={shape} alt="shape" className="img-fluid" />
                            </div>
                        </Col>
                        <Col className="ml-auto " md={6}>
                            <div className="shadow bg-white p-4">
                                <form
                                    onSubmit={handleLogin}>
                                    <div>
                                        <h4>{data.welcome}</h4>
                                        <p>{data.description}</p>
                                    </div>
                                    <div className="w-100 text-center mt-1 mb-2">
                                        <GoogleLogin
                                            className="border-0 shadow rounded"
                                            clientId="468100560729-5fbgm4t3v87f0v15kppflb2ct7qa143m.apps.googleusercontent.com"
                                            buttonText="Login with Google"
                                            onSuccess={responseGoogle}
                                            onFailure={responseGoogle}
                                            cookiePolicy={'single_host_origin'}
                                        />
                                    </div>
                                    <p className="text-center text-grey">{data.or}</p>
                                    <div>
                                        <div className="mb-3">
                                            <TextField
                                                size="small"
                                                fullWidth
                                                type="email"
                                                required
                                                onChange={handleChange('email')}
                                                label={data.userEmail}
                                                variant="outlined"
                                            />
                                            <div className="text-danger"><p className="mb-1">{this.state.errors.email}</p></div>
                                        </div>
                                        <div className="mb-3">
                                            <FormControl
                                                size="small"
                                                required
                                                fullWidth
                                                variant="outlined">
                                                <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                                                <OutlinedInput
                                                    id="outlined-adornment-password"
                                                    type={this.state.showPassword ? 'text' : 'password'}
                                                    value={this.state.password}
                                                    onChange={handleChange('password')}
                                                    endAdornment={
                                                        <InputAdornment position="end">
                                                            <IconButton
                                                                aria-label="toggle password visibility"
                                                                onClick={handleClickShowPassword}
                                                                onMouseDown={handleMouseDownPassword}
                                                                edge="end"
                                                            >
                                                                {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                                                            </IconButton>
                                                        </InputAdornment>
                                                    }
                                                    labelWidth={70}
                                                />
                                            </FormControl>
                                            <div className="text-danger"><p className="mb-1">{this.state.errors.password}</p></div>
                                        </div>
                                    </div>

                                    <div className="text-danger"><p className="mb-1">{this.state.errors.form}</p></div>
                                    <div className="text-green"><p className="mb-1">{this.state.errors.loginSucc}</p></div>
                                    <Row className="m-0 align-items-center">
                                        <Col md={6} className="text-left p-0">
                                            <FormControlLabel
                                                control={
                                                    <Checkbox
                                                        checked={this.state.rememberMe}
                                                        value={this.state.rememberMe}
                                                        onChange={handleChange('rememberMe')}
                                                        className="text-green"
                                                        color="primary"
                                                        size="small"
                                                    />
                                                }
                                                label={data.rememberMe}
                                            />
                                        </Col>
                                        <Col md={6} className="text-right p-0">
                                            <Link
                                                className="text-green"
                                                to={{
                                                    pathname: '/confirmation',
                                                    state: "passwordresets"
                                                }}
                                            >{data.forgetPassword}</Link>
                                        </Col>
                                        <Col md={12} className=" p-0">
                                            <Button
                                                fullWidth
                                                type="submit"
                                                className="btn-green">
                                                {data.loginBtn}
                                            </Button>
                                        </Col>
                                        <Col md={12} className="text-center pt-3">
                                            <div>
                                                <p className="">{data.dontHaveAcc}
                                                    <Link
                                                        className="text-green"
                                                        to='/registration'>
                                                        {data.createNew}
                                                    </Link>
                                                </p>
                                            </div>
                                        </Col>
                                    </Row>
                                </form>
                            </div>
                        </Col>
                    </Row>
                } />
        )
    }
}

export default withRouter(Login);



