import React from 'react';
import { BrowserRouter as Router, Switch, Route, HashRouter 
} from "react-router-dom";
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

import Home from './components/home/home.component';
import Login from './components/login/login.component';
import Registration from './components/login/registration.component';
import Reset from './components/login/reset-password.component';
import PinCode from './components/login/pin-code.component';
import Confirmation from './components/login/email-confirmation.component'
import HomeRegister from './components/home-register/home-register.component';
import BasicInformation from './components/instractor-profile/basic.information.component';
import WorkExperience from './components/instractor-profile/work-experience.component';
import Requirements from './components/instractor-profile/requirements.component'

import './App.css';

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#afd2b2',
      main: '#3C7A50',
      dark: '',
      contrastText: '#fff',
    }
  },
}); 

function App() {
  return (
    <ThemeProvider theme={theme}>
      <HashRouter>
            <Switch>
                <Route exact path="/">
                    <Home />
                </Route>
                <Route exact path="/login">
                    <Login />
                </Route>
                <Route exact path="/registration">
                    <Registration />
                </Route>
                <Route exact path="/reset">
                    <Reset />
                </Route>
                <Route exact path="/pinCode">
                    <PinCode />
                </Route>
                <Route exact path="/confirmation">
                    <Confirmation />
                </Route>
                <Route exact path="/home">
                    <HomeRegister />
                </Route>
                <Route exact path="/basicInformation">
                    <BasicInformation />
                </Route>
                <Route exact path="/workExperience">
                    <WorkExperience />
                </Route>
                <Route exact path="/requirements">
                  <Requirements />
                </Route>
            </Switch>
        </HashRouter>
    </ThemeProvider>
  );
}

export default App;
