import React, { Component } from 'react';
import { withRouter } from 'react-router';

import { Mutation } from 'react-apollo';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { createUploadLink } from 'apollo-upload-client';
import { ApolloClient } from "apollo-client";
import { ApolloProvider } from "react-apollo";
import { gql } from 'apollo-boost';


// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, InputLabel, FormControl, Select, FormLabel, RadioGroup, Radio,
    Button, MenuItem, Input, Checkbox, ListItemText, FormControlLabel
} from '@material-ui/core';
import pp from '../../images/intstractor-profile/pp.png';
import $ from 'jquery';

// language for test
import text from '../../languages/english/translate.json';

window.$ = window.jQuery = $;

class RequirementsForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            languageSpoken: [],
            preferedGender: "Male &amp; Female",
            preferedAge: "Adults &amp; Children",
            errors: {
                rate15: '',
                rate30: '',
                rate60: ''
            }
        }
    }
    fetchData = () => {
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            data: {
                query: `
        query{
            languageTypes{
            id
            context
            name
            created_at
            updated_at
            }
        }`
            },
            dataType: 'json',
            success: res => {
                console.log(res)
                if (res.data) {
                    if (res.data.languageTypes) {
                        console.log(res.data.languageTypes)
                        this.setState({ ...this.state, languageTypes: res.data.languageTypes })
                    }
                }
            },
            cache: err => console.log(err)
        });
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            data: {
                query: `
                query{
                    currencyTypes{
                    id
                    code
                    symbol
                    symbol_native
                    created_at
                    updated_at
                    }
                }`
            },
            dataType: 'json',
            success: res => {
                console.log(res)
                if (res.data) {
                    if (res.data.currencyTypes) {
                        console.log(res.data.currencyTypes)
                        this.setState({ ...this.state, currencyTypes: res.data.currencyTypes })
                    }
                }
            },
            cache: err => console.log(err)
        });
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            data: {
                query: `              
                query{
                    courseType{
                    id
                    context
                    name
                    created_at
                    updated_at
                    }
                }`
            },
            dataType: 'json',
            success: res => {
                console.log(res)
                if (res.data) {
                    if (res.data.courseType) {
                        console.log(res.data.courseType)
                        this.setState({ ...this.state, courseType: res.data.courseType })
                    }
                }
            },
            cache: err => console.log(err)
        });
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            data: {
                query: ` 
                query{
                    studentGenderTypes{
                      id
                      context
                      name
                      created_at
                      updated_at
                    }
                  }`
            },
            dataType: 'json',
            success: res => {
                console.log(res)
                if (res.data) {
                    if (res.data.studentGenderTypes) {
                        console.log(res.data.studentGenderTypes)
                        this.setState({ ...this.state, studentGenderTypes: res.data.studentGenderTypes })
                    }
                }
            },
            cache: err => console.log(err)
        });
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            data: {
                query: ` 
                query{
                  studentAgeTypes{
                    id
                    context
                    name
                    created_at
                    updated_at
                  }
                }`
            },
            dataType: 'json',
            success: res => {
                console.log(res)
                if (res.data) {
                    if (res.data.studentAgeTypes) {
                        console.log(res.data.studentAgeTypes)
                        this.setState({ ...this.state, studentAgeTypes: res.data.studentAgeTypes })
                    }
                }
            },
            cache: err => console.log(err)
        });
    }
    componentWillMount() {
        this.fetchData();
    }
    componentDidMount() {
        if (this.props.data) {
            console.log(this.props.data)
            this.setState({
                first_name: this.props.data.first_name, last_name: this.props.data.last_name,
                gender: this.props.data.gender, date_of_birth: this.props.data.date_of_birth, 
                email: this.props.data.email,
                profile : this.props.data.profile ,
                description: this.props.data.description, yearsOfExperience: this.props.data.yearsOfExperience,
                certificates: this.props.data.certificates, video: this.props.data.video
            })
        }
    }
    render() {
        let data = text["instractor-profile"].create.requirements;

        // activate uploading link on the oppollo 
        const apolloCache = new InMemoryCache()
        const token = (localStorage.getItem('jwt')) ? JSON.parse(localStorage.getItem('jwt')).access_token : '';

        const uploadLink = createUploadLink({
            uri: 'https://quranacademy.black-squares.com/graphql',
            headers: {
                authorization: token ? `Bearer ${token}` : "" 
            }
        })
        const client = new ApolloClient({
            cache: apolloCache,
            link: uploadLink
        })

        const handleChange = props => event => {
            const value = event.target.value;
            if(props === 'languageSpoken'){
                console.log(props);
                console.log(value)
                let languages = [] , id = [] ;
                for(let i = 0 ; i < value.length ; i ++){
                     languages.push(this.state.languageTypes.filter(language =>language.name === value[i])[0])
                }
                id = languages.map(x=>x.id);
                console.log(id);
                this.setState({ ...this.state, [props]: event.target.value  , languageSpokenId : id});
            }
            else
            {
                this.setState({ ...this.state, [props]: event.target.value });
            }

            let errors = this.state.errors;
            switch (props) {
                case 'rate15':
                    errors.rate15 =
                        value >= 0 || value === ""
                            ? ''
                            : text.validation.positive;
                    break;
                case 'rate30':
                    errors.rate30 =
                        value >= 0 || value === ""
                            ? ''
                            : text.validation.positive;
                    break;
                case 'rate60':
                    errors.rate60 =
                        value >= 0 || value === ""
                            ? ''
                            : text.validation.positive;
                    break;
                default:
                    break;
            }
        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleSubmit = (event) => {
            event.preventDefault();
            if (validateForm(this.state.errors)) {

            }
        }
        console.log(this.state);
        if (!this.state.studentAgeTypes || !this.state.languageTypes || !this.state.studentGenderTypes) {
            return (<div>Loading...</div>)
        }
        else {
            return (
                <ApolloProvider client={client}>
                <div className="shadow border">
                    <div className="border-bottom p-4 text-dark">
                        <Row className="">
                            <Col>
                                <h4>{data.title}</h4>
                                <p>{data.description}</p>
                            </Col>
                        </Row>
                    </div>
                    <div className="p-4">
                        <div className="mt-3 pb-3 text-lef">
                        <Mutation mutation={UPLOAD_FILE_STREAM}>
                        {(videoUpload, { d, loading }) => {
                            return (
                            <form onSubmit={e=>{
                                e.preventDefault();
                                let  password=  localStorage.getItem('password');
                                let token =  localStorage.getItem('token');

                                console.log(password)
                                console.log(token)
                                if (validateForm(this.state.errors)){
                                    if(password){
                                        videoUpload({
                                            variables: 
                                                { 
                                                first_name : this.state.first_name,
                                                last_name : this.state.last_name ,
                                                gender : this.state.gender ,
                                                date_of_birth : this.state.date_of_birth,
                                                years_of_experience : this.state.yearsOfExperience,
                                                description : this.state.description,
                                                rate_15_min: this.state.rate15,
                                                rate_30_min : this.state.rate30,
                                                rate_60_min : this.state.rate60,
                                                currency_type_id : this.state.currency,
                                                courses_types_id : this.state.courses ,
                                                student_gender_types_id : 1,
                                                student_age_types_id : 1,
                                                profile_vidoe  : this.state.video,
                                                profile_image: this.state.profile, 
                                                certificates :this.state.certificates,
                                                Spoken_Language_User :this.state.languageSpokenId,
                                                email : this.state.email,
                                                is_profile_public : false,
                                                password : "Moshera@99"
                                                }
                                            }).then((res, loading) => {
                                                console.log(res)
                                                if(res.data.globalRegisterInstructor.Status.code === 200){
                                                    this.props.history.push({
                                                        pathname : '/home'
                                                    })
                                                }
                                            }).catch((err) =>console.log(err));
                                        }
                                        else{
                                            videoUpload({
                                                variables: 
                                                    { 
                                                    first_name : this.state.first_name,
                                                    last_name : this.state.last_name ,
                                                    gender : this.state.gender ,
                                                    date_of_birth : this.state.date_of_birth,
                                                    years_of_experience : this.state.yearsOfExperience,
                                                    description : this.state.description,
                                                    rate_15_min: this.state.rate15,
                                                    rate_30_min : this.state.rate30,
                                                    rate_60_min : this.state.rate60,
                                                    currency_type_id : this.state.currency,
                                                    courses_types_id : this.state.courses ,
                                                    student_gender_types_id : 1,
                                                    student_age_types_id : 1,
                                                    profile_vidoe  : this.state.video,
                                                    profile_image: this.state.profile, 
                                                    certificates :this.state.certificates,
                                                    Spoken_Language_User :this.state.languageSpokenId,
                                                    is_profile_public : false,
                                                    token : token ,
                                                    provider : "google",
                                                }
                                                }).then((res, loading) => {
                                                    console.log(res)
                                                    if(res.data.globalRegisterInstructor.Status.code === 200){
                                                        this.props.history.push({
                                                            pathname : '/home'
                                                        })
                                                    }
                                                }).catch((err) =>console.log(err));
                                        }
                                    }
                                }}>
                                {/* Language spoken */}
                                <Row className="m-0 mb-3 ">
                                    <Col className="p-0">
                                        <InputLabel required>
                                            {data.languageSpoken  }
                                        </InputLabel>
                                        <FormControl size="small" fullWidth>
                                            <Select
                                                id="demo-mutiple-checkbox"
                                                required
                                                multiple
                                                variant="outlined"
                                                value={this.state.languageSpoken}
                                                onChange={handleChange('languageSpoken')}
                                                renderValue={(selected) => selected.join(', ')}
                                            >
                                                {this.state.languageTypes?.map((name) => (
                                                    <MenuItem key={name.name} value={name.name}>
                                                        <Checkbox color="primary" checked={this.state.languageSpoken.indexOf(name.name) > -1} />
                                                        <ListItemText primary={name.name} />
                                                    </MenuItem>
                                                ))}
                                            </Select>
                                        </FormControl>
                                    </Col>
                                </Row>
                                {/* Preferred students */}
                                <Row className="m-0 mb-3">
                                    {/* Preferred gender */}
                                    <Col md={12} className="mb-2 p-0">
                                        <FormControl component="fieldset">
                                            <FormLabel required className="mb-1" component="legend">{data.preferedStudents}</FormLabel>
                                            <RadioGroup
                                                row
                                                required
                                                onChange={handleChange('preferedGender')}
                                                value={this.state.preferedGender}
                                                aria-label="preferedStudents"
                                                name="preferedStudents">
                                                {this.state.studentGenderTypes?.map(type =>
                                                    <FormControlLabel
                                                        name="prefereGender"
                                                        value={type.name}
                                                        control={<Radio color="primary" />}
                                                        label={type.name}
                                                        labelPlacement="end"
                                                    />
                                                )}
                                            </RadioGroup>
                                        </FormControl>
                                    </Col>
                                    {/* Preferred  age */}
                                    <Col md="12" className="p-0">
                                        <FormControl component="fieldset">
                                            <FormLabel required className="mb-1" component="legend">{data.preferedAge}</FormLabel>
                                            <RadioGroup
                                                row
                                                
                                                value={this.state.preferedAge}
                                                onChange={handleChange('preferedAge')}
                                                aria-label="preferedAge"
                                                name="preferedAge">
                                                {this.state.studentAgeTypes?.map(type =>
                                                    <FormControlLabel
                                                        name="preferedAge"
                                                        value={type.name}
                                                        control={<Radio color="primary" />}
                                                        label={type.name}
                                                        labelPlacement="end"
                                                    />
                                                )}
                                            </RadioGroup>
                                        </FormControl>
                                    </Col>
                                </Row>
                                {/* Courses */}
                                <Row className="m-0 mb-3">
                                    <Col md={12} className="p-0">
                                        <InputLabel required id="courses" >
                                            {data.courses}
                                        </InputLabel>
                                        <FormControl size="small" fullWidth>
                                            <Select
                                                variant="outlined"
                                                native
                                                required
                                                name={data.courses}
                                                onChange={handleChange('courses')}
                                            >
                                                <option value=""></option>
                                                {this.state.courseType?.map(course =>
                                                    <option value={course.id}>{course.name}</option>
                                                )}
                                            </Select>
                                        </FormControl>
                                    </Col>
                                </Row>
                                {/* curency */}
                                <Row className="m-0 mb-3">
                                    <Col md={12}  className="p-0">
                                        <InputLabel  required id="courses" >
                                            {data.currency}
                                        </InputLabel>
                                        <FormControl size="small" fullWidth>
                                            <Select
                                                variant="outlined"
                                                native
                                                required
                                                name={data.courses}
                                                onChange={handleChange('currency')}
                                            >
                                                <option value=""></option>
                                                {this.state.currencyTypes?.map(currency =>
                                                    <option value={currency.id}>{currency.code}</option>
                                                )}
                                            </Select>
                                        </FormControl>
                                    </Col>
                                </Row>
                                {/* Rate per minutes */}
                                <Row className="m-0 mb-3">
                                    <Col md={12} className="p-0">
                                        <InputLabel required id="courses" >
                                            {data.rate}
                                        </InputLabel>
                                    </Col>
                                    <Col className="pr-1 pl-0 ">
                                        <TextField
                                            disabled
                                            value="15 MIN"
                                            fullWidth
                                            className="bg-muted"
                                            size="small"
                                            variant="outlined"
                                        />
                                    </Col>
                                    <Col className="pr-0 pl-1 ">
                                        <TextField
                                            inputProps={{
                                                min: 0
                                            }}
                                            fullWidth
                                            required
                                            className=""
                                            size="small"
                                            type="number"
                                            variant="outlined"
                                            onChange={handleChange('rate15')}
                                        />
                                        <div className="text-danger"><p className="mb-1">{this.state.errors.rate15}</p></div>
                                    </Col>
                                </Row>
                                <Row className="m-0 mb-3">
                                    <Col className="pr-1 pl-0 ">
                                        <TextField
                                            disabled
                                            value="30 MIN"
                                            fullWidth
                                            className="bg-muted"
                                            size="small"
                                            variant="outlined"
                                        />
                                    </Col>
                                    <Col className="pr-0 pl-1 ">
                                        <TextField
                                            fullWidth
                                            type="number"
                                            required
                                            inputProps={{
                                                min: 0
                                            }}
                                            className=""
                                            size="small"
                                            variant="outlined"
                                            onChange={handleChange('rate30')}
                                        />
                                        <div className="text-danger"><p className="mb-1">{this.state.errors.rate30}</p></div>
                                    </Col>
                                </Row>
                                <Row className="m-0 mb-3">
                                    <Col className="pr-1 pl-0 ">
                                        <TextField
                                            disabled
                                            value="60 MIN"
                                            fullWidth
                                            className="bg-muted"
                                            size="small"
                                            variant="outlined"
                                        />
                                    </Col>
                                    <Col className="pr-0 pl-1 ">
                                        <TextField
                                            fullWidth
                                            required
                                            inputProps={{
                                                min: 0
                                            }}
                                            type="number"
                                            className=""
                                            size="small"
                                            variant="outlined"
                                            onChange={handleChange('rate60')}
                                        />
                                        <div className="text-danger"><p className="mb-1">{this.state.errors.rate60}</p></div>
                                    </Col>
                                </Row>
                                {/* form errors */}
                                <div className="text-danger"><p className="mb-1">{this.state.errors.form}</p></div>
                                {/* back and save buttons */}
                                <Row className="m-0 align-items-center w-100 border-top pt-3">
                                    <Col className="text-left p-0">
                                        <Button
                                            onClick={e => {
                                                window.scrollTo({ top: "0", behavior: "smooth" })
                                                this.props.history.push({
                                                    pathname: "/workExperience"
                                                })
                                            }}
                                            className="btn-green-outline pl-4 pr-4">
                                            {data.backBtn}
                                        </Button>
                                    </Col>
                                    <Col className="text-right p-0">
                                        <Button
                                            type="submit"
                                            className="btn-green pl-4 pr-4">
                                            {data.saveBtn}
                                        </Button>
                                    </Col>
                                </Row>
                            </form>
                            )}}
                            </Mutation>
                        </div>
                    </div>
                </div>
                </ApolloProvider>
            )
        }
    }
}

const UPLOAD_FILE_STREAM = localStorage.getItem('password') ?
 gql`
mutation(
    $first_name : Name! 
   $last_name : Name!
   $gender  : Boolean
     $date_of_birth : Date
   $years_of_experience : Int!
   $description : Text!
    $rate_15_min : Int!
   $rate_30_min : Int!
    $rate_60_min : Int!
    $currency_type_id : Int!
   $courses_types_id  : Int!
      $student_gender_types_id : Int!
   $ student_age_types_id  : Int!
   $profile_vidoe : Upload!
    $profile_image : Upload!
    $certificates : [Upload!]
    $is_profile_public : Boolean!
    $Spoken_Language_User : [Int!]!
   $email : Email!
   $password : Password!
 )
 {
   globalRegisterInstructor(data: {
     InstructorsInput : {
           first_name : $first_name
     last_name : $last_name 
     gender : $gender 
     date_of_birth : $date_of_birth
     years_of_experience : $years_of_experience
     description : $description
      rate_15_min: $rate_15_min
     rate_30_min : $rate_30_min
     rate_60_min : $rate_60_min
     currency_type_id : $currency_type_id
     courses_types_id : $courses_types_id 
     student_gender_types_id : $student_gender_types_id
     student_age_types_id : $ student_age_types_id 
     is_profile_public : $is_profile_public
     profile_vidoe : $profile_vidoe
     profile_image :$profile_image
     certificates : $certificates
     Spoken_Language_User :$Spoken_Language_User
     }
     subInstructorsInput : {
       email : $email 
       password : $password
     }
   }){
     BearerTokenResponse{
       access_token
     }
     Student{
       email
     }
     Instructor{ 
     email
     }
     SubInstructor{
       email
     }
     Status{
       code
       message
     }
   }
 }
` :  gql`
mutation(
    $first_name : Name! 
   $last_name : Name!
   $gender  : Boolean
     $date_of_birth : Date
   $years_of_experience : Int!
   $description : Text!
    $rate_15_min : Int!
   $rate_30_min : Int!
    $rate_60_min : Int!
    $currency_type_id : Int!
   $courses_types_id  : Int!
      $student_gender_types_id : Int!
   $ student_age_types_id  : Int!
   $profile_vidoe : Upload!
    $profile_image : Upload!
    $certificates : [Upload!]
    $is_profile_public : Boolean!
    $Spoken_Language_User : [Int!]!
    $token : String!
    $provider : String!
 )
 {
   globalRegisterInstructor(data: {
     InstructorsInput : {
    first_name : $first_name
     last_name : $last_name 
     gender : $gender 
     date_of_birth : $date_of_birth
     years_of_experience : $years_of_experience
     description : $description
      rate_15_min: $rate_15_min
     rate_30_min : $rate_30_min
     rate_60_min : $rate_60_min
     currency_type_id : $currency_type_id
     courses_types_id : $courses_types_id 
     student_gender_types_id : $student_gender_types_id
     student_age_types_id : $ student_age_types_id 
     is_profile_public : $is_profile_public
     profile_vidoe : $profile_vidoe
     profile_image :$profile_image
     certificates : $certificates
     Spoken_Language_User :$Spoken_Language_User
     }
     SocialiteInput : {
        token : $token
        provider : $provider
      }
   }){
     BearerTokenResponse{
       access_token
     }
     Student{
       email
     }
     Instructor{ 
     email
     }
     SubInstructor{
       email
     }
     Status{
       code
       message
     }
   }
 }
`


export default withRouter(RequirementsForm)
