import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';

import Main from './main.component';

// graph ql
import { gql } from '@apollo/client';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import { TextField, Button } from '@material-ui/core';
import mail from '../../images/login/confirm.svg';
import $ from 'jquery';

// language for test
import text from '../../languages/english/translate.json';



class Confirmation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            codeFor : '',
            errors: {
                email: ''
            }
        }
    }
    componentDidMount(){
        console.log(this.props);
        if(this.props.location?.state){
            this.setState({...this.state , codeFor : this.props.location.state})
        }
    }
    render() {
        let data = text.emailConfirmation;
        const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);

        const handleChange = props => event => {
            const value = event.target.value;

            this.setState({ ...this.state, [props]: event.target.value });

            let errors = this.state.errors;
            errors.form = ''
            errors.sentSucc = ''
            switch (props) {
                case 'email':
                    errors.email =
                        validEmailRegex.test(value)
                            ? ''
                            : text.validation.email;
                    break;
                default:
                    break;
            }
        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleSendPinCode = (event) => {
            event.preventDefault();
            console.log(this.state);
            if (validateForm(this.state.errors) && this.state.codeFor) {
                $.ajax({
                    url: 'https://quranacademy.black-squares.com/graphql',
                    method: 'post',
                    data: {
                        query: `
                        mutation($email : Email! $sendPinCodeFor : sendPinCodeFor!){
                        globalSendPinCode(data:{
                            email : $email
                            sendPinCodeFor : $sendPinCodeFor
                        }){
                            Status{
                            code
                            message
                            }
                        }
                        }`,
                        variables: {
                            "email": this.state.email,
                            "sendPinCodeFor": this.state.codeFor
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        if (res.data) {
                            console.log(res.data.globalSendPinCode.Status)
                            if (res.data.globalSendPinCode.Status.code === 200) {
                                // pin sent successfully !
                                let errors = this.state.errors;
                                errors.sentSucc = res.data.globalSendPinCode.Status.message
                                this.setState({ ...this.state, errors: errors })
                                //  render pincode
                                this.props.history.push({
                                    pathname : "/pinCode",
                                    state : { 
                                        email :  this.state.email ,
                                        codeFor : this.state.codeFor
                                    }
                                });

                            } else {
                                let errors = this.state.errors;
                                errors.form = res.data.globalSendPinCode.Status.message
                                this.setState({ ...this.state, errors: errors })
                            }
                        }
                    },
                    cache: err => console.log(err)
                });
            }
            else {
                console.log("invalid form!");
                this.props.history.push({
                    pathname: '/login'
                });
            }
        }
        return (
            <Main
                content={
                    <Row className="alig-items-center justify-content-center mt-5 mb-5">
                        <Col md={4} className="bg-white shadow text-center pt-5 pb-5 pr-4 pl-4">
                            <form onSubmit={handleSendPinCode}>
                                <div className="mb-2">
                                    <img src={mail} alt="mail" className="img-flid" />
                                </div>
                                <div>
                                    <h4>{data.title}</h4>
                                    <p>{data.description}</p>
                                </div>
                                <div className="mb-3">
                                    <TextField
                                        size="small"
                                        fullWidth
                                        required
                                        onChange={handleChange('email')}
                                        label={data.email}
                                        variant="outlined"
                                    />
                                    <div className="text-danger text-left"><p className="mb-1">{this.state.errors.email}</p></div>
                                </div>
                                <div className="text-danger text-left"><p className="mb-1">{this.state.errors.form}</p></div>
                                <div className="text-green text-left"><p className="mb-1">{this.state.errors.sentSucc}</p></div>
                                <div className="">
                                    <Button
                                        fullWidth
                                        type="submit"
                                        className="btn-green">
                                        {data.continue}
                                    </Button>
                                </div>
                                <div>
                                    <div className="text-center mt-3">
                                        {data.haveAcc} <Link className="text-green" to="/login">{data.loginBtn}</Link>
                                    </div>
                                </div>
                            </form>
                        </Col>
                    </Row>
                } />
        )
    }
}

export default  withRouter(Confirmation)